<script>
  import { setContext as baseSetContext } from "svelte";
  import { fade } from "svelte/transition";
  import CircleButton from "./CircleButton.svelte";

  export let key = "simple-modal";
  export let closeButton = true;
  export let closeOnEsc = true;
  export let closeOnOuterClick = true;
  export let styleBg = { top: 0, left: 0 };
  export let styleWindow = {};
  export let styleContent = {};
  export let setContext = baseSetContext;
  export let transitionBg = fade;
  export let transitionBgProps = { duration: 250 };
  export let transitionWindow = transitionBg;
  export let transitionWindowProps = transitionBgProps;

  const defaultState = {
    closeButton,
    closeOnEsc,
    closeOnOuterClick,
    styleBg,
    styleWindow,
    styleContent,
    transitionBg,
    transitionBgProps,
    transitionWindow,
    transitionWindowProps
  };
  let state = { ...defaultState };

  let Component = null;
  let props = null;

  let background;
  let wrap;

  const camelCaseToDash = str =>
    str.replace(/([a-zA-Z])(?=[A-Z])/g, "$1-").toLowerCase();

  const toCssString = props =>
    Object.keys(props).reduce(
      (str, key) => `${str}; ${camelCaseToDash(key)}: ${props[key]}`,
      ""
    );

  $: cssBg = toCssString(state.styleBg);
  $: cssWindow = toCssString(state.styleWindow);
  $: cssContent = toCssString(state.styleContent);
  $: currentTransitionBg = state.transitionBg;
  $: currentTransitionWindow = state.transitionWindow;

  const open = (NewComponent, newProps = {}, options = {}) => {
    Component = NewComponent;
    props = newProps;
    state = { ...defaultState, ...options };
    document.body.style.overflow = "hidden";
  };

  const close = () => {
    Component = null;
    props = null;
    document.body.style.overflow = "visible";
  };

  const handleKeyup = ({ key }) => {
    if (state.closeOnEsc && Component && key === "Escape") {
      event.preventDefault();
      close();
    }
  };

  const handleOuterClick = event => {
    if (
      state.closeOnOuterClick &&
      (event.target === background || event.target === wrap)
    ) {
      event.preventDefault();
      close();
    }
  };

  setContext(key, { open, close });
</script>

<style>
  * {
    box-sizing: border-box;
  }

  .content {
    position: relative;
    max-height: 100vh;
    overflow: auto;
  }
</style>

<svelte:window on:keyup={handleKeyup} />

<div>
  {#if Component}
    <div
      class="bg-teal z-10 fixed inset-0"
      on:click={handleOuterClick}
      bind:this={background}
      transition:currentTransitionBg={state.transitionBgProps}
      style={cssBg}>
      <div bind:this={wrap}>
        <div
          transition:currentTransitionWindow={state.transitionWindowProps}
          style={cssWindow}>
          {#if state.closeButton}
            <div
              class="absolute z-20 top-0 right-0 mt-6 lg:mt-12 mr-4 lg:mr-20">
              <CircleButton
                onClick={close}
                src="images/icons/times.svg"
                alt="close button" />
            </div>
          {/if}
          <div
            class="content px-4 lg:px-20 pt-16 lg:pt-24 pb-6 lg:pb-32"
            style={cssContent}>
            <svelte:component this={Component} {...props} />
          </div>
        </div>
      </div>
    </div>
  {/if}
  <slot />
</div>
